import {Component, OnInit} from '@angular/core';
import {FirebaseListObservable} from "angularfire2";
import {NewsService} from "../services/news.service";
import {Observable} from "rxjs";
import {News} from "../models/news.model";

@Component({
  selector: 'app-newscontainer',
  templateUrl: './newscontainer.component.html',
  styleUrls: ['./newscontainer.component.css'],
  providers: [NewsService]
})
export class NewscontainerComponent implements OnInit {

  private newsList: Observable<any[]>;
  private numOfElements: number = 0;
  private end: number;
  private hasNext: boolean = false;
  private hasPrev: boolean = false;

  private last: string;
  private first: string;

  constructor(private newsService: NewsService) {
  }


  ngOnInit() {

    this.newsService.getSize().subscribe(size => {
      this.numOfElements = size;
      this.newsList = this.newsService.getFirst();
      this.newsList.subscribe((obj) => {
        this.last = obj[0]['createdDate'];
        this.first = obj[obj.length-1]['createdDate'];
        console.log(this.last);
      });

    });
  }

  next() {
    console.log(this.last);
    this.newsList = this.newsService.getThree(this.last);
  }

  prev() {
      this.newsList = this.newsService.getThree(this.first);

  }


}



