import {Component, OnInit, ViewChild} from '@angular/core';
import {MemberModalComponent} from "../member-modal/member-modal.component";
import {CoachModalComponent} from "../coach-modal/coach-modal.component";

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {

  @ViewChild('memberModal') modal: MemberModalComponent;
  @ViewChild('coachModal') cModal: CoachModalComponent;

  openTeamModal(member){
    this.modal.open(member);
  }

  openCoachModal(coach){
    this.cModal.open(coach);
  }

  constructor() { }

  ngOnInit() {
  }


}



